<template>
  <div>
    <el-dialog
      :title="addEditType === 'add' ? '添加权限' : '编辑权限'"
      :visible.sync="addDialogVisible"
      fullscreen
      center>
      <el-form
        :model="addForm"
        label-width="120px"
        ref="addForm"
        :rules="addFormRules"
        status-icon
        style="width: 40%; margin: auto;">
        <el-form-item label="所属系统" prop="systemId">
          <el-select v-model="addForm.systemId" clearable @change="getPermissionAttrList">
            <el-option v-for="item in availSystemAttrList" :key="item.id" :label="item.name"
                       :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="类型" prop="type">
          <el-select v-model="addForm.type" clearable>
            <el-option label="菜单" value="菜单"></el-option>
            <el-option label="按钮" value="按钮"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="名称" prop="name">
          <el-input v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="description">
          <el-input v-model="addForm.description" type="textarea" rows="5" resize="vertical"></el-input>
        </el-form-item>
        <el-form-item label="父菜单">
          <el-tree
            :data="permissionAttrList"
            :props="defaultProps"
            node-key="id"
            ref="treeRef"
            @check-change="checkChange"
            @node-click="nodeClick"
            highlight-current>
          </el-tree>
        </el-form-item>
        <el-form-item label="路由" prop="state" v-show="addForm.type=='菜单'">
          <el-input v-model="addForm.state"></el-input>
        </el-form-item>
        <el-form-item label="图标" prop="icon" v-show="addForm.type=='菜单'">
          <el-select v-model="addForm.icon" clearable>
            <el-option v-for="item in iconNames" :key="item" :label="item" :value="item">
              <i :class="item"></i>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="排序" prop="sort" v-show="addForm.type=='菜单'">
          <el-input-number v-model="addForm.sort" :min="0" :max="10"></el-input-number>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="default" @click="addEditPermissionCancel">取 消</el-button>
        <el-button type="primary" @click="addEditPermissionConfirm">确 定</el-button>
      </div>
    </el-dialog>


    <el-form :inline="true" :model="searchForm" ref="searchForm">
      <el-form-item label="类型" prop="type">
        <el-select v-model="searchForm.type" clearable @change="searchPermissionList">
          <el-option label="请选择" value=""></el-option>
          <el-option label="菜单" value="菜单"></el-option>
          <el-option label="按钮" value="按钮"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="名称" prop="name">
        <el-input v-model="searchForm.name" placeholder="名称" @input="searchPermissionList"></el-input>
      </el-form-item>
      <el-form-item label="所属系统" prop="systemId">
        <el-select v-model="searchForm.systemId" @change="searchPermissionList">
          <el-option label="请选择" value=""></el-option>
          <el-option v-for="item in systemAttrList" :key="item.id" :label="item.name" :value="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchPermissionList">查询</el-button>
        <el-button type="default" @click="searchFormReset">重置</el-button>
        <el-button type="success" @click="openPermissionDialog('add')">添加</el-button>
      </el-form-item>
    </el-form>


    <el-table border :data="list" style="width: 100%;" row-key="id" @sort-change="sortChange">
      <el-table-column label="ID" width="70" sortable="custom">
        <template slot-scope="scope">
          {{scope.row.id}}
        </template>
      </el-table-column>

      <el-table-column label="名称" width="180" sortable="custom">
        <template slot-scope="scope">
          {{scope.row.name}}
        </template>
      </el-table-column>

      <el-table-column label="描述" width="200">
        <template slot-scope="scope">
          {{ scope.row.description }}
        </template>
      </el-table-column>

      <el-table-column label="所属系统" width="180">
        <template slot-scope="scope">
          {{ scope.row.systemName }}
        </template>
      </el-table-column>

      <el-table-column label="类型" width="80">
        <template slot-scope="scope">
          <el-tag size="mini"
                  :type="scope.row.type==='菜单' ? 'info' : 'success'">
            {{ scope.row.type }}
          </el-tag>
        </template>
      </el-table-column>

      <el-table-column label="图标" width="60">
        <template slot-scope="scope">
          <i :class="scope.row.icon"></i>
        </template>
      </el-table-column>

      <el-table-column label="路由" width="180">
        <template slot-scope="scope">
          {{ scope.row.state }}
        </template>
      </el-table-column>

      <el-table-column label="排序" width="60">
        <template slot-scope="scope">
          {{ scope.row.sort }}
        </template>
      </el-table-column>

      <el-table-column label="操作时间" width="180">
        <template slot-scope="scope">
          {{ scope.row.operateTime }}
        </template>
      </el-table-column>

      <el-table-column label="操作人" width="100">
        <template slot-scope="scope">
          {{ scope.row.operator }}
        </template>
      </el-table-column>

      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button plain type="primary" size="mini" @click="openPermissionDialog('edit', scope.row)">编辑</el-button>
          <el-button plain type="danger" size="mini" @click="deletePermission(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import urls from '@/utils/urls';
  import iconNames from '@/icons/name';

  export default {
    name: "Permission",
    data() {
      return {
        systemAttrList: null,
        availSystemAttrList: null,
        permissionAttrList: null,


        checkedId:null,
        defaultProps: {
          children: 'children',
          label: 'name'
        },
        iconNames: iconNames,
        addEditType: 'add',
        addDialogVisible: false,
        addForm: {
          systemId: '',
          type: '',
          name: '',
          description: '',
          parentId: null,
          state: '',
          icon: '',
          sort: 0,
        },
        addFormRules: {
          systemId: [
            {required: true, message: '请选择所属系统', trigger: 'change'},
          ],
          type: [
            {required: true, message: '', trigger: 'change'},
          ],
          name: [
            {required: true, message: '请输入权限名称', trigger: 'blur'},
            {min: 2, max: 20, message: '长度在 2 到 20 个字符', trigger: 'blur'}
          ],
          description: [
            {min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur'}
          ]
        },


        searchForm: {
          type: '',
          name: '',
          systemId: ''
        },

        list: null,
        currentPage: 1,
        pageSize: 100,
        total: 1,
      }
    },
    mounted() {
      this.getSystemAttrList();
      this.searchPermissionList();

      this.getAvailSystemAttrList();
    },
    methods: {
      getSystemAttrList() {
        this.$get(urls.getSystemAttrList, null).then((response) => {
          let {data} = response;
          this.systemAttrList = data;
        })
      },
      getAvailSystemAttrList() {
        this.$get(urls.getSystemAttrList, {disableFlag: 0}).then((response) => {
          let {data} = response;
          this.availSystemAttrList = data;
        })
      },
      getPermissionAttrList() {
        this.permissionAttrList = null;
        this.addForm.parentId = null;
        let systemId = this.addForm.systemId;
        if (!systemId) {
          return;
        }
        return this.$get(urls.getPermissionAttrList, {systemId}).then((response) => {
          let {data} = response;
          this.permissionAttrList = data;
        })
      },


      searchPermissionList() {
        this.$getLoading(urls.searchPermissionList, {
          currentPage: this.currentPage,
          pageSize: this.pageSize,
          ...this.searchForm
        }).then((response) => {
          let {data} = response;
          this.list = data.list;
          this.total = data.total;
        })
      },
      searchFormReset() {
        this.$refs.searchForm.resetFields();
        this.searchPermissionList();
      },
      sortChange(column, prop, order) {
        console.log(column, prop, order);
      },



      checkChange(data, checked, node){
        if(checked == true){
          this.$refs.treeRef.setCheckedNodes([data]);
        }
      },
      nodeClick(data,checked,node){
        this.$refs.treeRef.setCheckedNodes([data]);

      },
      openPermissionDialog(type, row) {
        this.addDialogVisible = true;
        this.addEditType = type;
        if (row) {
          this.addForm = {
            id: row.id,
            systemId: row.systemId,
            type: row.type,
            name: row.name,
            description: row.description,
            parentId: row.parentId
          };
          this.getPermissionAttrList().then(function () {
            // 编辑时，旧的数据
            this.$refs.treeRef.setCheckedKeys([row.parentId]);
          }.bind(this));
          if (this.addForm.type == '菜单') {
            this.addForm.state = row.state;
            this.addForm.icon = row.icon;
            this.addForm.sort = row.sort;
          } else {
            this.addForm.state = '';
            this.addForm.icon = '';
            this.addForm.sort = 0;
          }
        }
      },
      addEditPermissionCancel() {
        this.addDialogVisible = false;
        this.$refs.addForm.resetFields();
      },
      addEditPermissionConfirm() {
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            if (this.addForm.type == '菜单') {
              if (!this.addForm.state) {
                this.$message.error('菜单类型权限需有路由！');
                return;
              }
              if (!this.addForm.icon) {
                this.$message.error('菜单类型需有icon！');
                return;
              }
              if (!this.addForm.sort || this.addForm.sort == 0) {
                this.$message.error('菜单类型需有排序，不能为0！');
                return;
              }
            }
            else if (this.addForm.type == '按钮') {
              /*if (!this.addForm.parentId) {
                this.$message.error('按钮类型权限需有父菜单！');
                return;
              }*/
            }
            let parentId = this.$refs.treeRef.getCheckedKeys();
            console.log(parentId);
            /*this.$postLoading(this.addEditType === 'add' ? urls.addPermission : urls.editPermission, {...this.addForm}).then((response) => {
              this.addEditPermissionCancel();
              this.searchPermissionList();
            })*/
          } else {
            return false;
          }
        });
      },


      deletePermission(roleId) {
        this.$confirm('确定删除该权限？', '警告', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$post(urls.deletePermission, {roleId}).then((response) => {
            this.searchPermissionList();
          })
        });
      }
    }
  }
</script>

